.fab-refresh-container {
    display: inline-block;
    width: var(--fab-size);
    height: var(--fab-size);
    box-shadow: var(--shadow-major);
    border-radius: 100px;
    background: var(--color-button-minor);
    padding: 0;
    transition-duration: 0.5s;
}
.fab-refresh-container.expanded {
    width: 300px;
    border-radius: var(--fab-border-radius);
    background: var(--color-button-major);
}
.fab-refresh-container .auth-button {
    height: 100%;
    border-radius: inherit;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
@media screen and (max-width: 400px) {
    .fab-refresh-container.expanded {
        width: 200px;
    }
    .fab-refresh-container .auth-button {
        white-space: break-spaces;
    }
}

.fab-refresh-container .fab {
    background: none;
    box-shadow: none;
    margin: 0;
}
.fab-refresh * {
    /* Initial state */
    transition: transform 0.5s ease-in-out;
    transform-origin: 50% -50%;
}
.fab-refresh:active * {
    /* Spin effect */
    transition: none;
    transform: rotate(-360deg);
}