.fab, .button-major.fab, .button-minor.fab {
    height: var(--fab-size);
    width: var(--fab-size);
    border: none;
    border-radius: var(--fab-border-radius);
    box-shadow: var(--shadow-major);
    margin-left: var(--separation-major);
    padding: 0;
}

.button-minor.fab {
    box-shadow: var(--shadow-major);
    border-radius: 100px;
}
.button-minor.fab .fab-icon * {
    fill: var(--color-on-button-minor);
}
.button-major.fab .fab-icon * {
    fill: var(--color-on-button-major);
}
.fab-icon {
    transform: translate(0, 7.5%);
    width: 50%;
    height: 50%;
}
