.context-menu-container {
    position: absolute;
    background: var(--background-card);
    border-radius: var(--radius-minor);
    box-shadow: var(--shadow-major);
    margin: 0 var(--separation-minor);
    right: 0;
    margin-top: calc(var(--separation-minor) * -3);
    z-index: 100;
    animation: menuIn 0.15s ease-out;

}
.context-menu {
    display: flex;
    flex-direction: column;
}
@media screen and (min-width: 800px) {
    .context-menu-container {
        right: calc(50vw - 400px);
    }
}
@keyframes menuIn {
    0% {
        opacity: 0;
        scale: 50%;
    }
    100% {
        opacity: 1;
        scale: 100%;
    }
}
@keyframes menuFadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.context-menu-close-button {
    z-index: -1;
    border: none;
    outline: none;
    position: fixed;
    background: #0005;
    animation:  menuFadeIn 0.3s ease-out;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}
.context-menu button {
    padding: calc(var(--padding-minor) / 2) var(--padding-minor);
    background: none;
    border: none;
    font-size: 1em;
    cursor: pointer;
    transition-duration: 0.2s;
    color: var(--color-text-major);
}
.context-menu button:first-child {
    border-top-left-radius: var(--radius-minor);
    border-top-right-radius: var(--radius-minor);
}
.context-menu button:last-child {
    border-bottom-left-radius: var(--radius-minor);
    border-bottom-right-radius: var(--radius-minor);
}
.context-menu button:hover, .context-menu button:focus {
    background: var(--color-button-subtle-hover);
}
.context-menu button:active {
    background: var(--color-button-subtle-pressed);
}