.pane.reachable {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    width: calc(100% - calc(var(--padding-major) * 2));
    min-height: 50vh;
    margin-bottom: 0;
}
/** Adjust view height to inverse of zoom */
@media screen and (max-width: 300px) {
    .pane.reachable {
        min-height: 66vh;
    }
}
@media screen and (max-width: 200px) {
    .pane.reachable {
        min-height: 100vh;
    }
}
.pane {
    background-color: var(--background-pane);
    border-radius: var(--radius-major);
    padding: var(--padding-major);
    margin-top: var(--separation-major);
    margin-bottom: var(--separation-major);
    transition: background-color 0.8s;
}
.fab-spacer {
    height: var(--fab-size);
    padding: var(--padding-major);
}

.pane-header {
    display: flex;
    flex-direction: row;
}
.pane-title {
    flex-grow: 1;
    font-size: 1.5em;
    font-weight: 500;
    margin-top: 0;
    margin-bottom: var(--separation-major);
    margin-left: calc(var(--separation-minor) * 2);
}
.pane-menu-button {
    height: 100%;
    /*margin-right: calc(var(--separation-minor) * 1.5);*/
    margin: -20px calc(var(--separation-minor) - 15px) -20px -20px;
    padding: 20px;
    background-clip: content-box;
    border-radius: 100px;
}
.pane-menu-button svg {
    height: 100%;
    translate: 0 6%;
}
.floating-action-button-container {
    display: flex;
    flex-direction: row;
    position: fixed;
    bottom: var(--fab-distance);
    right: var(--fab-distance);
    z-index: 100;
}

.pane-loading-text {
    margin: 40px auto auto;
    font-size: 2em;
    text-align: center;
}
.pane-flex {
    display: flex;
    flex-direction: column;
}