
.flex-activity {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;
    min-height: 100vh;
    width: 100%;
    margin: auto;
    max-width: 800px;
}

.flex-image {
    width: 100%;
    height: 0;
    border-radius: calc(var(--button-height) / 2);
    margin: var(--padding-major);
    flex-grow: 3;
    object-fit: cover;
}

/** Simple resize logic to scale for small / medium / large screens */
@media screen and (max-width: 450px) {
    body {
        font-size: 87.5%;
        zoom: 87.5%;

        --fab-size: 62px;
        --fab-border-radius: 13px;
        --fab-distance: 30px;
    }
    .flex-activity {
        min-height: 114.2857142vh;
    }
    .flex-image {
        flex-grow: 2;
    }
}

@media screen and (max-width: 300px) {
    body {
        font-size: 75%;
        zoom: 75%;

        --fab-size: 75px;
        --fab-border-radius: 18px;
        --fab-distance: 32px;
    }
    .flex-activity {
        min-height: 133.3333333vh;
    }
}
@media screen and (max-width: 200px) {
    body {
        font-size: 50%;
        zoom: 50%;

        --fab-size: 85px;
        --fab-border-radius: 22px;
        --fab-distance: 28px;
    }
    .flex-activity {
        min-height: 200vh;
    }
}

.above-reachable {
    flex-grow: 1;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    width: auto;
}
.section-header {}

.section-floating-info {
    margin: 10% auto;
    display: block;
    max-width:70%;
    background: var(--background-pane);
    padding: var(--padding-major);
    border-radius: var(--radius-major);
}
.bottom-container .section-mini {
    width: auto;
    background: var(--background-pane);
    padding: 10px 25px;
    border-radius: var(--radius-major);
    text-align: center;
}

.header-main {
    margin: 0;
    font-weight: 400;
    font-size: 3.5em;
}
.header-alt {
    margin: 0;
    font-weight: 500;
    font-size: 2.5em;
}
.header-sub {
    margin: 0;
    font-weight: 600;
}

.section-centered {
    width: calc(100% - var(--padding-centered) * 2);
    padding: var(--padding-centered);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
}
.section-centered p,
.section-centered ul,
.section-centered div,
.section-centered button {
    width: 100%;
}

.text-small {
    font-size: 0.9em;
    font-weight: 500;
}
.header-minor {
    font-size: 1.2em;
    margin: 5px;
    font-weight: normal;
}
.bottom-container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;
}
.bottom-container>label {
    height: auto;
}

.button-subtle {
    background-color: var(--color-button-subtle);
    border: none;
    border-radius: var(--radius-major);
}
.button-subtle:hover {
    background-color: var(--color-button-subtle-hover);
}
.button-subtle:active {
    background-color: var(--color-button-subtle-pressed);
}
.button-subtle-icon, .button-subtle-icon * {
    fill: var(--color-text-major);
}
.button-subtle-wrapper {
    background: none;
    border: none;
}

.button-major, .button-minor, .button-scary {
    height: var(--button-height);
    border-radius: 100px;
    border: none;
    padding: 0 var(--padding-major);
    font-size: 1.2em;
    font-weight: bold;
    cursor: pointer;
}
.button-major {
    background: var(--color-button-major);
    color: var(--color-on-button-major);
}
.button-major:hover {
    background: var(--color-button-major-hover);
}
.button-major:active {
    background: var(--color-button-major-pressed);
}
.button-minor {
    background: var(--color-button-minor);
    color: var(--color-on-button-minor)
}
.button-minor:hover {
    background: var(--color-button-minor-hover);
}
.button-minor:active {
    background: var(--color-button-minor-pressed);
}
.button-scary {
    background: var(--color-button-scary);
    color: var(--color-on-button-scary);
}
.button-scary:hover {
    background: var(--color-button-scary-hover);
}
.button-scary:active {
    background: var(--color-button-scary-pressed);
}


input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    height: 26px;
    width: 26px;
    min-width: 26px;
    border-radius: 100px;
    border: none;
    font-size: 1.2em;
    font-weight: bold;
    cursor: pointer;
    margin-right: 8px;
    background: var(--color-button-minor);
}
input[type="checkbox"]:checked {
    background: var(--color-button-major);
}
label {
    cursor: pointer;
    margin: 10px 0;
}
input[type="checkbox"]:hover {
    background: var(--color-button-minor-hover);
}
input[type="checkbox"]:active {
    background: var(--color-button-minor-pressed);
}
input[type="checkbox"]:checked:hover {
    background: var(--color-button-major-hover);
}
input[type="checkbox"]:checked:active {
    background: var(--color-button-major-pressed);
}
input[type="checkbox"]:checked::before {
    content: "";
    position: relative;
    display: block;
    width: 6px;
    height: 11px;
    border-bottom: 4px solid var(--color-on-button-major);
    border-right: 4px solid var(--color-on-button-major);
    transform: translate(80%, 25%) rotate(45deg);
    font-size: 1.5em;
    color: #fff;
}

.loading-text {
    margin: 40px auto auto;
    font-size: 2em;
    text-align: center;

    background: linear-gradient(90deg, #DDD, #666, #DDD);
    background-size: 20em 100%;
    animation: loadingTextFadeIn 1.5s ease-in-out, loadingTextWipe 3s infinite linear;
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;

    -webkit-user-select: none;
    user-select: none;
}

@keyframes loadingTextFadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes loadingTextWipe {
    0% {
        background-position: -20em 0;
    }
    100% {
        background-position: 20em 0;
    }
}

.floating-info-header {
    font-weight: 500;
}
.floating-info-text, .floating-info-header {
    font-size: 1em;
    text-align: start;
    margin: 10px;
}