@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');
body {
  font-optical-sizing: auto;
  font-style: normal;

  background-color: #FFF;

  /* Text */
  --color-text-major: #000;
  color: var(--color-text-major);

  /* Layout */
  --padding-major: 15px;
  --padding-minor: 20px;
  --radius-major: 25px;
  --radius-minor: 15px;
  --separation-major: 15px;
  --separation-minor: 10px;
  --shadow-major: 0 1px 10px rgba(0, 0, 0, 0.5);
  --shadow-minor: 0 0 5px rgba(0, 0, 0, 0.2);

  --padding-centered: 40px;

  /* Backgrounds */
  --background-pane: #EEE;
  --background-card: #FFF;
  --background-unselected: #EEE;
  --background-unselected-hover: #CCC;
  --background-selected: #FFF;
  --color-on-selected: #000;
  --color-on-unselected: #000;

  /* Colors for sentiments */
  --color-negative-strong: #bf2e2e;
  --color-neutral-strong: #1d5ed8;
  --color-positive-strong: #30a330;
  --color-negative-weak: #e6bcbc;
  --color-neutral-weak: #b0c3da;
  --color-positive-weak: #afd6af;

  /** Buttons */
  --button-height: 50px;

  /* Major buttons: Highlighted, commonly used */
  --color-button-major: #1d5ed8;
  --color-button-major-hover: #1f71dd;
  --color-button-major-pressed: #467df6;
  --color-on-button-major: #FFF;

  /* Minor buttons: Uncommonly used */
  --color-button-minor: #b0c3da;
  --color-button-minor-hover: #9fafc5;
  --color-button-minor-pressed: #8795a8;
  --color-on-button-minor: #1b2733;

  /* Scary buttons: Represent possibly dangerous actions */
  --color-button-scary: #d64963;
  --color-button-scary-hover: #bf3f55;
  --color-button-scary-pressed: #a1485b;
  --color-on-button-scary: #FFF;
  --color-description: #333;

  /* Subtle buttons: Buttons without a background */
  --color-button-subtle: none;
  --color-button-subtle-hover: #CCC;
  --color-button-subtle-pressed: #888;

  /* Progress bars */
  --bar-color-positive: var(--color-positive-weak);
  --bar-color-negative: var(--color-negative-weak);
  --bar-separation-major: 5px;
  --bar-separation-minor: 5px;
  --bar-height: 15px;
  --bar-radius: 10px;

  /* Floating action buttons */
  --fab-size: 54px;
  --fab-border-radius: 13px;
  --fab-distance: 30px;

  transition: background-color 1s;
}
.dark-mode {
  background-color: #111;
  --background-pane: #222;
  --background-card: #333;
  --background-selected: #c3d2f3;
  --background-unselected: #555;
  --background-unselected-hover: #666;
  --color-on-selected: #000;
  --color-on-unselected: #FFF;
  --color-text-major: #FFF;
  --color-text-minor: #AAA;
  --color-description: #AAA;
  --color-positive-weak: #3b603b;
  --color-positive-strong: #49c649;
  --color-negative-weak: #714747;
  --color-negative-strong: #e82f2f;
  --color-neutral-weak: #435877;
  --color-neutral-strong: #447bfb;
  --color-button-minor: #343f51;
  --color-button-minor-hover: #434f6a;
  --color-button-minor-pressed: #556d9e;
  --color-on-button-minor: #a4baec;
  --color-button-subtle-hover: #333;
  --color-button-subtle-pressed: #555;
}