.floating-info-horizontal {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    padding: 0;
}
.floating-info-main {
    display: flex;
    flex-direction: column;
    padding: 0;
}
.floating-info-main .floating-info-text {
    margin: 0;
    padding: 1px 10px 20px 25px;
}
.floating-info-main .floating-info-header {
    font-size: 1.2em;
    margin: 0;
    padding: 20px 10px 1px 25px;
    border-bottom-left-radius: var(--radius-major);
}
.floating-info-button {
    margin: 0;
    height: auto;

    border-radius: 0 var(--radius-major) var(--radius-major) 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.floating-info-button * {
    fill: var(--color-on-button-minor);
}