.async-action-button-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    transition-duration: 0.5s;
}

.async-action-button-container.waiting {
    opacity: 0.5;
}
.async-action-button-container.waiting>*:not(.button-minor) {
    pointer-events: none;
}

.async-action-button-container>*:not(:last-child) {
    margin-right: var(--separation-minor);
}
.async-action-button-container>*:not(.button-minor) {
    flex-grow: 1;
}

.async-action-button-container>*{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.async-action-button-container .button-minor .action-button-icon {
    fill: var(--color-on-button-minor);
}
.async-action-button-container .action-button-icon {
    display: inline;
    height: 100%;
}
.async-action-button-container>.button-scary {
    flex-grow: 0.1;
}