.form-card {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.form-container {
    flex: 1;
    display: flex;
    flex-direction: column;
}
.form-container * {
    font-size: 1.1em;
}

.form-container>* {
    flex: 1;
    max-height: 80px;
}
.form-container>*:not(:last-child) {
    margin-bottom: var(--separation-minor);
}

.text-entry-container {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.form-container input, .form-container select {
    height: 100%;
    border: none;
    outline: none;
    box-shadow: var(--shadow-minor);
    border-radius: var(--radius-minor);
    padding: 10px 15px;
    color: var(--color-on-selected);
    background: var(--background-selected);
    flex: 1;
}
input:disabled, select:disabled {
    color: var(--color-on-unselected);
    background: var(--background-unselected);
}
.form-container select {
    width: 100%;
    padding-left: 2vh;
    font-size: 1.2em;
    text-align: center;
}
.form-container .custom-select-container {
    min-height: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.form-container .custom-select-container .custom-select-desc {
    font-size: 0.9em;
    margin: 5px;
    min-height: 1em;
    text-align: center;
    line-height: 0.9em;
    color: var(--color-description);
}


input[type="number"] {
    height: calc(100% - 40px);
    width: 60px;
    margin-right: 10px;
    text-align: center;
}
label {
    padding: 0;
    height: 100%;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.form-grow {
    display: block;
    flex-grow: 1;
}
.form-spacer-minor {
    display: block;
    height: var(--separation-minor);
}
.form-spacer-major {
    display: block;
    height: var(--separation-major);
}
.form-button-container {
    min-height: 60px;
}
.button-disabled {
    opacity: 0.5;
    pointer-events: none;
}