.histogram-item-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    height: 100%;
    flex-grow: 1;
    padding: 2px 20px;

}
.histogram {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    flex-grow: 1;
}
.histogram-empty {
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
}
.histogram-empty-text {
    -webkit-user-select: none;
    user-select: none;
    margin: 0 8px;
    color: var(--color-description);
    font-weight: 550;
    line-height: 0.9em;
}
.histogram-empty-line {
    -webkit-user-select: none;
    user-select: none;
    flex-grow: 1;
    margin: 0 7px;
    width: 100px;
    height: 2px;
    background: var(--color-description);
}
.histogram-empty.sentiment-neutral .histogram-empty-line {
    background: var(--color-neutral-weak);
}
.histogram-empty.sentiment-neutral .histogram-empty-text {
    color: var(--color-neutral-weak);
}
.histogram-empty.sentiment-positive .histogram-empty-line {
    background: var(--color-positive-weak);
}
.histogram-empty.sentiment-positive .histogram-empty-text {
    color: var(--color-positive-weak);
}
.histogram-empty.sentiment-negative .histogram-empty-line {
    background: var(--color-negative-weak);
}
.histogram-empty.sentiment-negative .histogram-empty-text {
    color: var(--color-negative-weak);
}

.histogram-item {
    display: flex;
    height: 0;
    flex-grow: 1;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    border-bottom: 2px solid var(--background-pane);
    min-height: 35px;
}

.histogram-label {
    width: 100px;
    display: flex;
    flex-direction: column;
}
.histogram-item .histogram-count {
    width: 30px;
}

.histogram-item .progress-bar {
    margin: 0;
    flex-grow: 1;
}
.histogram-label-prefix {
    font-size: 0.8em;
    margin-bottom: -4px;
    color: var(--color-description);
}
.histogram-item:last-child {
    border-bottom: none;
}
.histogram-item>div:last-child {
    width: 0;
    flex-grow: 1;
}

.histogram-header {
    padding: 15px 20px 5px;
    border-bottom: 2px solid var(--background-pane);
    font-size: 1.2em;
}
.histogram-header-first {
    line-height: 0.9em;
    padding-bottom: 0;
    padding-top: 10px;
}
.histogram-subheader {
    padding: 2px 20px 10px;
    font-size: 0.9em;
    color: var(--color-description);
    border-bottom: 2px solid var(--background-pane);
}