.progress-bar {
    height: var(--bar-height);
    margin-top: 5px;
    display: flex;
    flex-direction: row;
}
.progress-bar .segment {
    height: 100%;
    background-color: var(--color-button-minor);
    flex: 1;
    margin-left: calc(var(--bar-separation-minor) / 2);
    margin-right: calc(var(--bar-separation-minor) / 2);
}
.progress-bar .segment.compact {
    background: red;
    margin-left: 1px;
    margin-right: 2px;
}
.progress-bar .segment.dense {
    background: red;
    margin-left: 0;
    margin-right: 1px;
}
.progress-bar .segment.filled {
    background-color: var(--color-button-major);
}
.progress-bar .segment.left {
    border-top-left-radius: var(--bar-radius);
    border-bottom-left-radius: var(--bar-radius);
    margin-left: 0;
}
.progress-bar .segment.left.overflow {
    margin-left: var(--bar-separation-major);
}
.progress-bar .segment.right {
    border-top-right-radius: var(--bar-radius);
    border-bottom-right-radius: var(--bar-radius);
    margin-right: 0;
}
.progress-bar.sentiment-positive .segment {
    background-color: var(--color-positive-weak);
}
.progress-bar.sentiment-negative .segment {
    background-color: var(--color-negative-weak);
}
.progress-bar.sentiment-neutral .segment {
    background-color: var(--color-neutral-weak);
}
.progress-bar.sentiment-positive .segment.filled {
    background-color: var(--color-positive-strong);
}
.progress-bar.sentiment-negative .segment.filled {
    background-color: var(--color-negative-strong);
}
.progress-bar.sentiment-neutral .segment.filled {
    background-color: var(--color-neutral-strong);
}