.status-text.sentiment-positive {
    color: var(--color-positive-weak);
}
.status-text.sentiment-negative {
    color: var(--color-negative-weak);
}
.status-text.sentiment-neutral {
    color: var(--color-neutral-weak);
}
.status-text.sentiment-positive.sentiment-strong {
    color: var(--color-positive-strong);
}
.status-text.sentiment-negative.sentiment-strong {
    color: var(--color-negative-strong);
}
.status-text.sentiment-neutral.sentiment-strong {
    color: var(--color-neutral-strong);
}
.status-text.sentiment-strong {
    font-weight: bold;
}

.card {
    margin-bottom: var(--separation-major);
    padding: var(--padding-minor);
    background: var(--background-card);
    border-radius: var(--radius-minor);
    box-shadow: var(--shadow-minor);
    transition: background-color 0.3s ease-out;
}
.card:last-child {
    margin-bottom: 0;
}

.header-habit {
    margin: 0 0 var(--separation-minor) 0;
    font-size: 1.2em;
    font-weight: 500;
}
.card {
    animation: slideIn 0.1s ease-out;
}

@keyframes slideIn {
    0% {
        opacity: 0;
        scale: 80%;
    }
    100% {
        opacity: 1;
        scale: 100%;
    }
}