/* Prevent google popup from overflowing */
.activity-auth iframe {
    max-width: 100%;
}

label {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    padding: 8px;
    margin-right: 10px;
}
@media screen and (max-width: 600px) {
    label {
        font-size: 1.2em;
    }

    .header-minor {
        font-size: 1.2em;
    }
}
@media screen and (max-width: 250px) {
    label {
        font-size: 1em;
    }
}
.header-minor, .button-major {
    font-size: 1.3em;
}

.flex-activity-loading-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.activity-loading-text {
    margin: auto;
}

.above-reachable {
    display: flex;

    align-items: center;
    justify-content: center;
    flex-direction: column;
    /*flex-grow: 1;*/
    text-align: center;
    width: auto;
}
.section-header {
    margin-top: 80px;
}