.error-message.hidden {
    transform: scale(0%);
    max-height: 0;
    padding: 0;
    margin: 0;
}
.error-message {
    max-height: 50px;
    transition: 0.2s ease-in-out;
    font-weight: bold;
    display: block;
    text-align: center;
    padding: var(--padding-minor);
    border-radius: var(--radius-major);
    background: rgba(255, 17, 17, 0.4);
}