.text-switch-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 38px;
    margin: 1px 0;
    box-shadow: var(--shadow-minor);
    border-radius: var(--radius-major);
}
.text-switch-item {
    flex: 1;
    border: none;
    outline: none;
    min-height: 35px;
    color: var(--color-on-unselected);
    background: var(--background-unselected);
    transition-duration: 0.1s;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0;
}
.text-switch-item:not(.text-switch-selected):hover {
    background: var(--background-unselected-hover);
}
.text-switch-item::after {
    content: "";
    display: inline-block;
    width: 2px;
}

.text-switch-item::before {
    content: "";
    display: inline-block;
    position: relative;
    left: -1px;
    height: 80%;
    width: 2px;
    background: #8888;
}
.text-switch-item:first-child::before, .text-switch-selected+.text-switch-item::before, .text-switch-selected::before {
    background: none;
}
.text-switch-item:first-child {
    border-top-left-radius: var(--radius-major);
    border-bottom-left-radius: var(--radius-major);
}
.text-switch-item:last-child {
    border-top-right-radius: var(--radius-major);
    border-bottom-right-radius: var(--radius-major);
}
.text-switch-selected {
    box-shadow: var(--shadow-minor);
    color: var(--color-on-selected);
    background: var(--background-selected);
    z-index: 1;
    justify-content: space-between;
    margin: -1px 0;
}